.swal-modal {
    background-color: #eee;
    border: none;
    font-family: inherit;
    border-radius: 0;
    padding: 0 20px 10px;
}

.swal-button {
    background-color: #222;
    font-family: inherit;
}

.swal-button:not([disabled]):hover {
    background-color: #222;
    opacity: 0.8;
}